"use client";

import { useState } from "react";
import { homeopathyQnA } from "./questions";

export default function HowHomeopathyWorks() {
  const [active, setActive] = useState(0);

  return (
    <div className="flex flex-col md:flex-row gap-10">

      {/* LEFT SIDEBAR */}
      <aside className="w-full md:w-1/3 bg-card border border-border rounded-xl p-6 h-fit sticky top-24">
        <h3 className="text-xl font-bold text-foreground mb-4">Topics</h3>

        <ul className="space-y-2">
          {homeopathyQnA.map((item, index) => (
            <li key={index}>
              <button
                onClick={() => setActive(index)}
                className={`
                  w-full text-left px-4 py-3 rounded-lg transition 
                  ${active === index 
                    ? "bg-primary text-primary-foreground shadow-sm" 
                    : "bg-background text-foreground hover:bg-secondary/60"}
                `}
              >
                {item.question}
              </button>
            </li>
          ))}
        </ul>
      </aside>

      {/* RIGHT CONTENT */}
      <article className="flex-1 bg-card border border-border p-8 rounded-xl shadow-sm">
        <h2 className="text-3xl font-bold text-foreground mb-4">
          {homeopathyQnA[active].question}
        </h2>

        <p className="text-muted-foreground leading-relaxed text-lg whitespace-pre-line">
          {homeopathyQnA[active].answer}
        </p>

        <p className="mt-6 text-sm text-primary font-medium">
          üìå Reference: {homeopathyQnA[active].reference}
        </p>
      </article>

    </div>
  );
}
